       //
       initTemplate = (props,callback) => {
        let that = this;
        createUIDom(props)(
            {
                pagecode:props.config.pageCode//id
            // appcode:props.config.appcode//id
            },
            {
                moduleId: '10100SSS',domainName: 'uapbd'
            },
            (data, langData)=>{ //(data, langData)
                if(langData){
                    that.state.json = langData;
                    props.config.title = that.props.MutiInit.getIntl("10100SSS") && that.props.MutiInit.getIntl("10100SSS").get('10100SSS-000035');/*  */
                    that.state.orgTypeName = that.props.MutiInit.getIntl("10100SSS") && that.props.MutiInit.getIntl("10100SSS").get('10100SSS-000000');/*  */
                }
                if (data) {
                		//
                    let ccontext = data.context || {};
                    that.state.curOrg = {
                        refpk : ccontext.pk_org,
                        refname : ccontext.org_Name,
                        display : ccontext.org_Name,
                        values : {refpk : ccontext.pk_org, refname : ccontext.org_Name}
                    };
                    if(data.template){
                        let meta = data.template;
                        let buttonAry =[];
                        data.button.map((obj)=>{
                            if(obj.hasOwnProperty('area')&&obj.hasOwnProperty('key')&&obj.area ==='line-button-area'){
                                buttonAry.push(obj.key);
                            }
                        });
                        modifierMeta(props, meta,buttonAry)
                        props.meta.setMeta(meta);
                    }
    
                    if(data.button){
                        let button = data.button;
                        props.button.setButtons(button);
                        props.button.setPopContent('linedel', that.state.json['10100SSS-000024']); /* '' */
                        props.button.setButtons(button);
                    }
                    callback && callback();
                }
            }
        )
}

